*,
:before,
:after {
  margin: 0;
  padding: 0;
  color: inherit;
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  background-color: #f4f4f5;
  font-family: 'Libre Franklin', sans-serif;
  font-size: 16px;
  color: #4a4e57;
}
h1 {
  font-weight: 700;
  font-size: 38px;
}
h2 {
  font-weight: 300;
  font-size: 28px;
  text-transform: uppercase;
}
h3 {
  font-weight: 300;
  font-size: 21px;
}
h4 {
  font-weight: 700;
  font-size: 16px;
}
h5 {
  font-weight: 400;
  font-size: 10px;
  text-transform: uppercase;
}
small {
  font-size: 13px;
}
[grid] {
  display: flex;
}
[grid] [column-width] {
  flex: 1;
}
[grid] [column-width="1"] {
  width: 8.333333333333332%;
  max-width: 8.333333333333332%;
}
[grid] [column-width="2"] {
  width: 16.666666666666664%;
  max-width: 16.666666666666664%;
}
[grid] [column-width="3"] {
  width: 25%;
  max-width: 25%;
}
[grid] [column-width="4"] {
  width: 33.33333333333333%;
  max-width: 33.33333333333333%;
}
[grid] [column-width="5"] {
  width: 41.66666666666667%;
  max-width: 41.66666666666667%;
}
[grid] [column-width="6"] {
  width: 50%;
  max-width: 50%;
}
[grid] [column-width="7"] {
  width: 58.333333333333336%;
  max-width: 58.333333333333336%;
}
[grid] [column-width="8"] {
  width: 66.66666666666666%;
  max-width: 66.66666666666666%;
}
[grid] [column-width="9"] {
  width: 75%;
  max-width: 75%;
}
[grid] [column-width="10"] {
  width: 83.33333333333334%;
  max-width: 83.33333333333334%;
}
[grid] [column-width="11"] {
  width: 91.66666666666666%;
  max-width: 91.66666666666666%;
}
[grid] [column-width="12"] {
  width: 100%;
  max-width: 100%;
}
.constrain {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}
header {
  background-color: #dbf0f4;
}
